Blockly.Blocks.sd_init = {
    helpUrl: "http://www.hello-blocks.com/web/site/doc",
    shopUrl: "",
    init: function() {
        this.setColour(Blockly.Blocks.sd.HUE);
        this.appendDummyInput("").appendField(new Blockly.FieldImage(img_path + "arduino/sd.png", 30, 30)).appendField(Blockly.Msg.SD_BEGIN + " (SPI)").appendField(Blockly.Msg.PIN + " CS").appendField(new Blockly.FieldDropdown(profile["default"].digital),
            "CS");
        this.setPreviousStatement(!0, null);
        this.setNextStatement(!0, null)
    }
};
