import Blockly from '../../blockly/blockly';
import {profile} from '../generator/arduino';

Blockly.Blocks.io = {};
Blockly.Blocks.io.HUE = 230;

/**
 * ***************************************************************************
 * IO_PINMODE block definition
 * ***************************************************************************
 */
Blockly.Blocks.io_pinmode = {
    helpUrl: "http://www.hello-blocks.com/web/site/doc",
    shopUrl: "",
    init: function() {
        this.setColour(Blockly.Blocks.io.HUE);
        this.setColour(Blockly.Blocks.io.HUE);
        this.appendDummyInput()
            .appendField(Blockly.Msg.IO_PINMODE)
            .setAlign(Blockly.ALIGN_RIGHT)
            .appendField(new Blockly.FieldVariable(Blockly.Msg.VARIABLES_DEFAULT_NAME), "VAR")
            .appendField(Blockly.Msg.IO_PINMODE_AS).appendField(new Blockly.FieldDropdown([
                ["OUTPUT", "OUTPUT"],
                ["INPUT", "INPUT"],
                ["INPUT_PULLUP", "INPUT_PULLUP"]
            ]),
            "MODE");
        this.setPreviousStatement(!0, null);
        this.setNextStatement(!0, null)
    }
};

/**
 * ***************************************************************************
 * IO_DIGITAL_WRITE block definition
 * ***************************************************************************
 */
Blockly.Blocks.io_digital_write = {
    helpUrl: "http://www.hello-blocks.com/web/site/doc",
    shopUrl: "",
    init: function() {
        this.setColour(Blockly.Blocks.io.HUE);
        this.setColour(Blockly.Blocks.io.HUE);
        this.appendDummyInput()
            .appendField(Blockly.Msg.IO_DIGITAL_WRITE + " " + Blockly.Msg.PIN)
            .appendField(new Blockly.FieldDropdown(profile["default"].digital), "PIN")
            .appendField(Blockly.Msg.LOGIC_STAT)
            .appendField(new Blockly.FieldDropdown(profile["default"].logic_levels), "STAT");
        this.setPreviousStatement(!0, null);
        console.warn("Hola");
        this.setNextStatement(!0, null)
    }
};

