/* eslint-disable */
import Blockly from '../../blockly/blockly';
import {profile, img_path} from '../generator/arduino';

Blockly.Blocks.serial = {};
Blockly.Blocks.serial.HUE = 170;
Blockly.Blocks.serial_init = {
    helpUrl: "http://www.hello-blocks.com/web/site/doc",
    shopUrl: "",
    init: function() {
        this.setColour(Blockly.Blocks.serial.HUE);
        this.appendDummyInput("")
          .appendField(new Blockly.FieldImage("img/icons/conection.svg", 25, 25, { alt: "*", flipRtl: "FALSE" }))

//oJo            .appendField(new Blockly.FieldImage(img_path + "arduino/console.png", 25, 25))
            .appendField(Blockly.Msg.SERIAL_INIT).appendField(Blockly.Msg.SERIAL_BAUDRATE)
            .appendField(new Blockly.FieldDropdown([
                ["2400", "2400"],
                ["4800", "4800"],
                ["9600", "9600"],
                ["19200", "19200"],
                ["38400", "38400"],
                ["57600", "57600"],
                ["115200", "115200"]
            ]),
            "BAUD");
        this.setInputsInline(!0);
        this.setPreviousStatement(!0, null);
        this.setNextStatement(!0, null)
    }
};
Blockly.Blocks.serial_timeout = {
    helpUrl: "http://www.hello-blocks.com/web/site/doc",
    shopUrl: "",
    init: function() {
        this.setColour(Blockly.Blocks.serial.HUE);
        this.appendDummyInput("")
          .appendField(new Blockly.FieldImage("img/icons/conection.svg", 25, 25, { alt: "*", flipRtl: "FALSE" }))
//oJo            .appendField(new Blockly.FieldImage(img_path + "arduino/console.png", 25, 25));
        this.appendValueInput("MS", "Number").setCheck("Number").setAlign(Blockly.ALIGN_RIGHT)
            .appendField(Blockly.Msg.SERIAL_TIMEOUT);
        this.setInputsInline(!0);
        this.setPreviousStatement(!0, null);
        this.setNextStatement(!0, null)
    }
};
Blockly.Blocks.serial_print = {
    helpUrl: "http://www.hello-blocks.com/web/site/doc",
    shopUrl: "",
    init: function() {
        this.setColour(Blockly.Blocks.serial.HUE);
        this.appendDummyInput("")
          .appendField(new Blockly.FieldImage("img/icons/conection.svg", 25, 25, { alt: "*", flipRtl: "FALSE" }))
//oJo            .appendField(new Blockly.FieldImage(img_path + "arduino/console.png", 25, 25))
            .appendField(Blockly.Msg.SERIAL_PRINT);
        this.appendValueInput("STRINGOUTPUT", "String");
        this.appendDummyInput("")
            .appendField(new Blockly.FieldCheckbox("TRUE"), "NEWLINE")
            .appendField(Blockly.Msg.ADD_NEW_LINE);
        this.setInputsInline(!0);
        this.setPreviousStatement(!0,
            null);
        this.setNextStatement(!0, null)
    }
};
