Blockly.Blocks.serial_interrupt = {
    helpUrl: "http://www.hello-blocks.com/web/site/doc",
    shopUrl: "",
    init: function() {
        this.setColour(Blockly.Blocks.serial.HUE);
        this.appendDummyInput()
            .appendField(Blockly.Msg.SERIAL_INT)
            .appendField(new Blockly.FieldDropdown(profile["default"].serialInt), "INT");
        this.appendStatementInput("DO");
        this.setInputsInline(!1);
        this.setPreviousStatement(!1);
        this.setNextStatement(!1)
    }
};
Blockly.Blocks.serial_plot = {
    helpUrl: "http://www.hello-blocks.com/web/site/doc",
    shopUrl: "",
    init: function() {
        this.setColour(Blockly.Blocks.serial.HUE);
        this.appendDummyInput("")
            .appendField(new Blockly.FieldImage(img_path + "arduino/console_plotter.png", 52, 25))
            .appendField(Blockly.Msg.SERIAL_PLOT);
        this.appendValueInput("PLOTVAR", "String").setAlign(Blockly.ALIGN_RIGHT);
        this.appendValueInput("PLOTVALUE", "Number").setCheck("Number").setAlign(Blockly.ALIGN_RIGHT).appendField(Blockly.Msg.SERIAL_PLOTVALUE);
        this.setInputsInline(!0);
        this.setPreviousStatement(!0, null);
        this.setNextStatement(!0, null)
    }
};
