import Vue from 'vue';
import Vuex from 'vuex';
import { FeathersVuex } from './feathers-client';

Vue.use(Vuex);
Vue.use(FeathersVuex);

const requireModule = require.context(
  // The path where the service modules live
  './services',
  // Whether to look in subfolders
  false,
  // Only include .js files (prevents duplicate imports`)
  /\.js$/,
);
const servicePlugins = requireModule
  .keys()
  .map((modulePath) => requireModule(modulePath).default);

const store = new Vuex.Store({
  state: {
    serialShelf: false,
    codeShelf: false,
    clickedSaveButton: false,
    clickedImportButton: false,
    clickedScreenCaptureButton: false,
    donateMenu: false,
    serialTab: 'program',
    currentServer: window.localStorage.currentServer || null,
    currentBoard: window.localStorage.currentBoard || '51b2904f-b7fd-3df8-9330-0ef1b852b816',
    currentProject: window.localStorage.currentProject || null,
    currentFile: window.localStorage.currentFile || null,
    importFile: null,
    blocklySaved: false,
  },
  mutations: {
    toggleSerialShelf(state, val) {
      state.serialShelf = typeof val === 'boolean' ? val : !state.serialShelf;
    },
    toggleDonateMenu(state, val) {
      state.donateMenu = typeof val === 'boolean' ? val : !state.donateMenu;
    },
    clickedSave(state) {
      state.clickedSaveButton = true;
    },
    clearSaveState(state) {
      state.clickedSaveButton = false;
    },
    clickedImport(state) {
      state.clickedImportButton = true;
    },
    clickedScreenCapture(state) {
      state.clickedScreenCaptureButton = true;
    },
    clearScreenShootState(state) {
      state.clickedScreenCaptureButton = false;
    },
    clearImportState(state) {
      state.clickedImportButton = false;
    },
    setSerialTab(state, val) {
      state.serialTab = val;
    },
    setCodeTab(state, val) {
      state.serialTab = val;
    },
    setCurrentServer(state, uuid) {
      window.localStorage.currentServer = uuid;
      state.currentServer = uuid;
    },
    setCurrentBoard(state, uuid) {
      window.localStorage.currentBoard = uuid;
      state.currentBoard = uuid;
    },
    setCurrentProject(state, uuid) {
      window.localStorage.currentProject = uuid;
      state.currentProject = uuid;
    },
    setCurrentFile(state, uuid) {
      window.localStorage.currentFile = uuid;
      state.currentFile = uuid;
    },
    setBlocklySaved(state, val) {
      state.blocklySaved = typeof val === 'boolean' ? val : !state.serialShelf;
    },
    toggleCodeShelf(state, val) {
      state.codeShelf = typeof val === 'boolean' ? val : !state.codeShelf;
    },
    setImportFile(state, file) {
      state.importFile = file;
    },
  },
  getters: {
    serialShelf(state) { return state.serialShelf; },
    codeShelf(state) { return state.codeShelf; },
    clickedSaveButton(state) { return state.clickedSaveButton; },
    clickedImportButton(state) { return state.clickedImportButton; },
    clickedScreenCaptureButton(state) { return state.clickedScreenCaptureButton; },
    donateMenu(state) { return state.donateMenu; },
    serialTab(state) { return state.serialTab; },
    currentServer(state) { return state.currentServer; },
    currentBoard(state) { return state.currentBoard; },
    currentProject(state) { return state.currentProject; },
    currentFile(state) { return state.currentFile; },
    currentBlocklySaved(state) { return state.blocklySaved; },
    importFile(state) { return state.importFile; },
  },
  actions: {

  },
  modules: {

  },

  plugins: [
    ...servicePlugins,
  ],
});

export default store;
